function script() {
  const redirect = (url) => {
    window.location.href = url;
  };

  const addNumber = (number) => {
    let currentNumber = parseInt(number.innerText);
    number.innerText = currentNumber < 99 ? currentNumber + 1 : 99;
  };

  const subNumber = (number) => {
    let currentNumber = parseInt(number.innerText);
    number.innerText = currentNumber > 0 ? currentNumber - 1 : 0;
  };

  document.querySelectorAll(".btn-redirect").forEach((item) => {
    item.addEventListener("click", function () {
      redirect(this.dataset.url);
    });
  });

  document.querySelectorAll(".btn-minus").forEach((item) => {
    item.addEventListener("click", function () {
      subNumber(this.nextElementSibling);
    });
  });

  document.querySelectorAll(".btn-plus").forEach((item) => {
    item.addEventListener("click", function () {
      addNumber(this.previousElementSibling);
    });
  });

  document.querySelectorAll(".btn-remove").forEach((item) => {
    item.addEventListener("click", function () {
      this.closest(".row").remove();
    });
  });
}

export default script;
